.container {
    display: flex;
    font-family: Arial;
    color: #005CB9;
    font-size: medium;
    justify-content: center;
    
}

@media only screen and (max-width: 500px) {
    .container {
        flex-direction: column;
    }
}

.textbox-title{
    flex: 2;
    font-family: Arial;
    color: rgb(2, 103, 204);
    font-size: xx-large;
    font-style: oblique <angle>;
}

.textbox-main {
    flex: 2;
    font-size: large;
    font-family: Arial;
    color: rgb(73, 153, 233);
    font-style: oblique <angle>;
    padding: -10%;
}

.textbox-sub{
    flex: 2;
    font-size: medium;
    font-family: Arial;
    color: #005CB9;
    font-style: oblique <angle>;
    padding: 2%;
}

.aside-1 {
    flex: 1;
}

.aside-2 {
    flex: 1;
    width: 5%;
}

.logos{
    width: 22%;
    /* background-color: white; */
}